.itemBox {
  /* background-color: antiquewhite; */
  margin: 3rem;
  padding: 1.5rem;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 15rem;
  height: 20rem;
}

.imageContainer {
  /* background-color: aliceblue; */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 100%;
  height: 60%;
  margin-bottom: 1rem;
}

img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.priceAndRating {
  display: flex;
  width: 80%;
  justify-content: space-between;
}
